/*-----------------------------------------------
|   Form
-----------------------------------------------*/

.search {
  padding: 10px 20px 10px 74px;
  color:rgba(18,72,56,0.50);
  border: 1px solid rgba(18, 72, 56, 0.70);
  border-radius: 40px;
  outline: none !important;
  box-shadow: none;
  &:focus{
    border: 1px solid map-get($theme-colors,'success');
    color: map-get($theme-colors,'success');
    box-shadow: inset 0 1px 2px rgba(18, 72, 56,.25),0 0 0 .25rem rgba(18, 72, 56, .3);
  }
}
.search::placeholder{
  color:rgba(18,72,0,0.50);
}

.search-icon{
  position: absolute;
  top: 50%;
  left: 25px;
  transform: translateY(-50%);
  width: fit-content;

  span {
    font-size: 24px;
    color: map-get($theme-colors, 'success');
    opacity: 60%;
  }
}

.form{
  max-width: 350px;
  width: 100%;

  @include media-breakpoint-up(lg){
    max-width: 369px;
  }
}

.email-input {
  padding: 15px 22px !important;
  border-radius: 8px !important;
  border: 1px solid #E7E9ED !important;
  color: rgba($black, 0.70);
  font-size: map-get($font-sizes, 9 );
  transition: all 0.15s ease-in;
  width: 100%;
  
  &:focus {
    color: #31374a !important;
    background-color: $white !important;
    border-color: #5de7a2 !important;
    outline: 0 !important;
    box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0), inset 0 0 0 30px $white, 0 0 0 0.25rem rgba(map-get($theme-colors, 'success'), 0.25) !important;
  }
  &::placeholder{
    color: rgba($black, 0.70);
    font-size: map-get($font-sizes, 8 );;
  }
}